main {
		margin: 1em 0;
		
		.item {
				-webkit-display: flex;
				-ms-display: flex;
				display: flex;
				-webkit-flex-direction: row;
				-ms-flex-direction: row;
				flex-direction: row;
				-webkit-justify-content: flex-start;
				-ms-justify-content: flex-start;
				justify-content: flex-start;
				margin: 0.5em 0;
				padding-left: 1em;
				
				.btn {
						margin: 0 0.5em;
				}
				
				input {
						padding-left: 0.5em;
						width: 80%;
				}
		}
}

#clean {
		text-align: center;
		margin-top: 5em;
}

.item input {
		border: none;
		background-color: $porcelain;
}